}
.introImg {
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: center;
  object-fit: cover;
}
.mask {
  height: 100vh;
  width: 100%;
  position: relative;
}
.introImg {
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: center;
  object-fit: cover;
}
.mask::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #000;
  opacity: 0.4;
}
.hero {
  height: 100%;
  width: 100%;
}
.hero .content {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  text-align: center;
}
.hero .content h1 {
  font-size: 4rem;
  padding: 0.6rem 0 1.5rem;
}
.hero .content p {
  font-size: 1.4rem;
  font-weight: 400;
  text-transform: uppercase;
}
@media screen and( max-width: 640px) {
  .hero .content h1 {
    font-size: 3rem;
  }
  .hero .content p {
    font-size: 1.4rem;
  }
}
